import React from 'react';
import { Typography } from 'antd';

const { Title, Paragraph } = Typography;

const Projetos = () => (
  <div>
    <Title level={2}>Meus Projetos</Title>
    <div>
      <Title level={3}>Projeto 1</Title>
      <Paragraph>Descrição breve do projeto.</Paragraph>
    </div>
    <div>
      <Title level={3}>Projeto 2</Title>
      <Paragraph>Descrição breve do projeto.</Paragraph>
    </div>
  </div>
);

export default Projetos;